import { Img, Text, Button } from "components";
import { useNavigate } from "react-router-dom";

const BlogPreview = () => {
  const navigate = useNavigate();

  return (
    <>
      <div className="flex flex-1 flex-col items-center justify-start w-full">
        <div className="flex flex-col items-start justify-start w-full">
          {/* blog header */}
          <div className="flex flex-row font-publicsans sm:gap-10 items-center justify-between w-full">
            <div className="flex flex-row gap-3.5 items-center justify-center ">
              <Img
                className="h-[7rem] w-[7rem] md:h-auto rounded-full"
                src="images/img_ellipse5.png"
                alt="EllipseFive"
              />
              <div className="flex flex-col items-start justify-start">
                <Text
                  className="text-bluegray-600 text-4xl"
                  size="txtPublicSansSemiBold18"
                >
                  By Jhone Leonardo
                </Text>
                <Text
                  className="mt-[5px] text-bluegray-600 text-2xl"
                  size="txtPublicSansLight14"
                >
                  12 September, 2020
                </Text>
              </div>
            </div>
            <Text
              className="text-bluegray-600 text-2xl text-right"
              size="txtPublicSansLight14"
            >
              <span className="text-bluegray-600 font-publicsans font-light">
                Category{" "}
              </span>
              <span className="text-pink-300 font-merriweather  text-2xl font-black">
                Writing
              </span>
            </Text>
          </div>

          {/* blog heading */}
          <Text
            className="italic mt-[3rem] text-7xl sm:text-[3.8rem] md:text-[4.4rem] text-gray-600 leading-normal w-full"
            size="txtMerriweatherBoldItalic48"
          >
            Consistent way of working to train yourself
          </Text>
          {/* blog cover image */}
          <Img
            className="h-[39rem] sm:h-auto mt-[3rem] object-cover rounded-2xl w-full"
            src="images/img_rectangle11.png"
            alt="RectangleEleven"
          />
          {/* blog content mini */}
          <Text
            className="leading-[3rem] mt-[3rem] text-bluegray-900 text-2xl w-full"
            size="txtPublicSansLight18"
          >
            We all know that every person has his or her own desire to work, but
            do you know that we can actually be consistent in making works if we
            want, now in this article I want to invite all of you to learn to be
            consistent in creating.
          </Text>

          {/* blog post time, like and share button */}
          <div className="flex flex-row font-publicsans gap-[5rem] sm:gap-0 items-center justify-around mt-10 w-fit sm:w-full">
            <div className="flex flex-row sm:flex-col gap-2.5 items-center sm:w-full">
              <Button
                className="flex h-[5rem] w-[5rem] sm:h-[4rem] sm:w-[4rem]  p-4 items-center justify-center rounded-full"
                color="light_blue_50"
              >
                <Img
                  className="h-[5rem] sm:h-[4rem]"
                  src="images/img_clock.svg"
                  alt="clock"
                />
              </Button>
              <Text
                className="text-black-900 text-xl"
                size="txtPublicSansLight14Black900"
              >
                5 minutes ago
              </Text>
            </div>
            <div className="flex flex-row sm:flex-col gap-2.5 items-center sm:w-full">
              <Button
                className="flex h-[5rem] w-[5rem] sm:h-[4rem] sm:w-[4rem] p-4 items-center justify-center rounded-full"
                color="lime_50"
                size="md"
              >
                <Img
                  className="h-[5rem] sm:h-[4rem]"
                  src="images/img_checkmark.svg"
                  alt="checkmark"
                />
              </Button>
              <Text
                className="text-black-900 text-xl"
                size="txtPublicSansLight14Black900"
              >
                12 Like
              </Text>
            </div>
            <div className="flex flex-row sm:flex-col gap-2.5 items-center sm:w-full">
              <Button
                className="flex h-[5rem] w-[5rem] sm:h-[4rem] sm:w-[4rem] p-4 items-center justify-center rounded-full"
                color="red_50"
                size="md"
              >
                <Img
                  className="h-[5rem] sm:h-[4rem]"
                  src="images/img_location.svg"
                  alt="location"
                />
              </Button>
              <Text
                className="text-black-900 text-xl"
                size="txtPublicSansLight14Black900"
              >
                Share
              </Text>
            </div>
          </div>
          {/* read full blog button */}
          <Button
            className="common-pointer cursor-pointer flex items-center justify-center gap-8 min-w-[25rem] my-8 py-[2.5rem] hover:bg-blue-200 hover:text-blue-800"
            onClick={() => navigate("/blogdetails")}
            shape="round"
            color="gray_200"
          >
            {
              <Img
                className=""
                src="images/img_arrowright.svg"
                alt="arrow_right"
              />
            }
            <div className="font-bold font-publicsans leading-[normal] sm:pl-5 text-left text-2xl tracking-[0.12px]">
              Continue Reading
            </div>
          </Button>
        </div>
      </div>
    </>
  );
};

export default BlogPreview;
