import { List } from "components";
import React, { Suspense } from "react";
import BlogPreview from "./BlogPreview";
import Pagination from "components/Pagination";
import BlogSidebar from "./BlogSidebar";
// const Pagination = React.lazy(() => import("components/Pagination"));
// const BlogPreview = React.lazy(() => import("components/Blogs/BlogPreview"));
// const BlogSidebar = React.lazy(() => import("components/Blogs/BlogSidebar"));

const Blogs = () => {
  return (
    <>
      <div className="flex sm:flex-col flex-row gap-[3rem] items-start justify-start max-w-[1110px] mx-auto sm:px-5 w-full">
        <Suspense>
          {/* blogs and pagination container */}
          <div className="flex sm:flex-1 flex-col gap-4 justify-start w-[66%] sm:w-full">
            {/* list of blogs */}
            <List
              className="flex flex-col gap-[5rem] items-center w-full"
              orientation="vertical"
            >
              <BlogPreview />
              <BlogPreview />
              <BlogPreview />
            </List>

            {/* pagination */}
            <Pagination />
          </div>
        </Suspense>

        {/* side bar */}
        <BlogSidebar />
      </div>
    </>
  );
};

export default Blogs;
