import React from "react";
import { Button, Img, Text } from "components";
import { Link } from "react-router-dom";
const PodcastCard = ({ podcastContent }) => {
  const { podcastTitle, coverImgURL, author } = podcastContent;

  return (
    <>
      <Link to="/bloggerdetails">
        <div className="bg-white-A700 flex flex-1 flex-col items-center justify-start p-[1.2rem] rounded-[1rem] shadow-bs2 w-full">
          <div className="flex flex-col gap-[1.5rem] items-center justify-start my-[0.5rem] rounded-[0.5rem] w-full">
            <div
              className="bg-cover bg-no-repeat flex flex-col h-[20rem] items-center justify-start p-[6rem] md:px-10 sm:px-5 rounded-[0.5rem] w-full"
              style={{
                backgroundImage: `url(${coverImgURL})`,
              }}
            >
              <Button
                className="flex h-[7rem] w-[7rem] items-center justify-center mb-[1rem] rounded-[50%]"
                shape="circle"
                color="gray_600_76"
                size="xl"
              >
                <Img
                  className="h-[4rem]"
                  src="images/img_group.svg"
                  alt="Group"
                />
              </Button>
            </div>
            <div className="flex flex-col gap-2.5 items-center justify-start w-full">
              <Text
                className="text-2xl text-gray-600 w-full"
                size="txtMerriweatherBold16"
              >
                {podcastTitle}
              </Text>
              <Text
                className="w-full text-bluegray-600 text-xl text-right"
                size="txtPublicSansLight14"
              >
                By {author}
              </Text>
            </div>
          </div>
        </div>
      </Link>
    </>
  );
};

export default PodcastCard;
